document.addEventListener("DOMContentLoaded",()=>{const e=document.querySelectorAll(".logos-carousel__carousel .splide");if(e)for(var o=0;o<e.length;o++)new Splide(e[o],{type:"loop",perPage:7,perMove:7,arrows:!1,pagination:!0,breakpoints:{[tablet]:{perPage:3,perMove:3},[ldpi]:{perPage:5,perMove:5}}}).mount()});